<template>
    <!-- 材料消耗信息 -->
    <div class="heigth-div" ref="wrapHeight">
        <div ref="bar_chart" class="bar-chart" :style = "{width:wrapWidth+'px',height:wrapHeight+'px'}"></div>
    </div>

    <!-- <div class="heigth-div scrollBar">
        <table class="el-table self-tableI adjust-tableI">
            <tr>
                <th><div class="cell">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div></th>
                <td><div class="cell">上月</div></td>
                <td><div class="cell">本月</div></td>
            </tr>
        </table>
        <table class="el-table self-table adjust-tableI">
            <tr v-for="(item,index) in items" :key="index">
                <th class="is-center"><div class="cell">{{item.matname}}</div></th>
                <td class="is-center"><div class="cell">{{item.consumeqty_lastmonth}}</div></td>
                <td class="is-center"><div class="cell">{{item.consumeqty}}</div></td>
            </tr>
        </table>
    </div> -->
</template>
<script>
    const echarts = require('echarts/lib/echarts');
    require('echarts/lib/chart/bar');
    require('echarts/lib/chart/line');
    require('echarts/lib/component/tooltip');
    require('echarts/lib/component/legend');
    export default {
        data() {
            return {
                items:[],
                itemsLast:[],
                itemsName:[],
                wrapHeight: 210,
                wrapWidth: 329,
            }
        },
        mounted() {
            this.init();
            this.getDivHeight();
        },
        methods: {
            init(){
                this.getlist();
            },
            getDivHeight(){
                this.wrapHeight = this.$refs.wrapHeight.offsetHeight+10;
                this.wrapWidth = this.$refs.wrapHeight.offsetWidth;
            },

            //获取数据
            getlist(){
                let tempData = [];
                this.items = [],
                this.itemsLast = [],
                this.itemsName = [],
                this.getAjax('/mktmfc/forms/getmatconsumemsg')
                .then((result)=>{
                    if(result.status === 200){
                        tempData = result.data.matConsumemsg;
                        if(tempData.length === 0){
                            this.itemsName = ['水泥','1-2石','减水剂'];
                            this.itemsLast = [0,0,0];
                            this.items = [0,0,0];
                        }
                        for(let i=0; i<tempData.length; i++){
                            this.itemsName.push(tempData[i].matname)
                            this.itemsLast.push(tempData[i].consumeqty_lastmonth)
                            this.items.push(tempData[i].consumeqty)
                        }
                    }else {
                        this.$message({
                            message:this.returnIntercept(result),
                            type: 'error'
                        });
                    }
                })
                .then(()=>{
                    this.initEchart();
                    this.setEchart(this.itemsName,this.items,this.itemsLast);
                })
            },

            initEchart(){
                this.bar_chart = echarts.init(this.$refs.bar_chart);
            },

            //传参
            setEchart(itemsName,items,itemsLast) {
                //bar
                this.bar_chart.setOption({
                    tooltip : {
                        trigger: 'axis',
                        axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                            type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                        }
                    },
                    legend: {
                        data:['本月','上月'],
                        right: '7%',
                    },
                    grid: {
                        left: '16%',
                        bottom: '20%'
                    },
                    xAxis : [
                        {
                            type: 'value',
                            axisTick: {
                                alignWithLabel: true
                            }
                        }
                    ],
                    yAxis : [
                        {
                            type: 'category',
                            name: '',
                            data:itemsName,
                            axisTick: {
                                alignWithLabel: true
                            }
                            //boundaryGap: [0, '100%']
                        },
                    ],
                    series : [
                        {
                            name:'本月',
                            type:'bar',
                            barWidth: '30%',
                            itemStyle: {
                                normal: {
                                    color: '#e7bcf3',
                                    shadowColor: 'rgba(0, 0, 0, 0.5)',
                                    shadowBlur: 10
                                }
                            },
                            label: {
                                normal: {
                                    show: true,
                                    position: 'right'
                                }
                            },
                            data:items
                        },
                        {
                            name:'上月',
                            type:'bar',
                            itemStyle: {
                                normal: {
                                    color: '#fb7293',
                                    shadowColor: 'rgba(0, 0, 0, 0.5)',
                                    shadowBlur: 10
                                }
                            },
                            label: {
                                normal: {
                                    show: true,
                                    position: 'right'
                                }
                            },
                            data:itemsLast
                            
                        }
                    ]
                });
            }
        }
    }
</script>
<style lang="less" scoped type="text/less">
    .self-table {
        th,td {
            border: 0px solid #fff;
            font-weight: normal;
            text-align: center;
            @media screen and (max-width: 1500px) {
                text-align: left;
                text-indent: 5px;
            }
        }
        th {
            border-right: 1px solid #cccccc;
            width: 35%;
        }
    }
    .self-tableI{
       text-align: center;
        @media screen and (max-width: 1500px) {
            text-align: left;
            text-indent: 5px;
        } 
    }
    .el-table td, .el-table th{
        background-color: #fff;
        height: 30px;
        @media screen and (max-width: 1500px) {
            height: 25px;
        }
    }
    .heigth-div{
        height: 100%;
        //overflow-x:auto
    }
    .bar-chart {
        /*width: 420px;*/
        /*height: 350px;*/
    }
</style>



